import Vue from 'vue'
import VueRouter from 'vue-router'
import AboutPage from '../views/about/AboutPage'
import App from '../views/index/IndexPage'

Vue.use(VueRouter)

export default new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: App
    },
    {
      path: '/about',
      component: AboutPage
    },
    {
      path: '/curriculum',
      component: AboutPage
    },
    {
      path: '/faq',
      component: AboutPage
    },
    {
      path: '/contact',
      component: AboutPage
    }
  ],
  scrollBehavior() {
    return { x: 0, y: 0 }
  }
})
