<template>
  <v-app-bar class="z-100" app elevation="0" :color="isScrolling ? 'white' : 'transparent'">
    <div class="d-flex align-center">
      <a :href="`/${baseUrl}`">
        <v-img
          alt="woowahan techcourse Logo"
          class="shrink mr-2"
          contain
          :src="getLogoUrl"
          transition="scale-transition"
          width="200"
        />
      </a>
    </div>
    <v-spacer></v-spacer>
    <template v-if="smAndUp">
      <v-btn
        v-for="menu in navMenus"
        :href="`${baseUrl}/${menu.link}`"
        :key="menu._id"
        text
        :dark="!isScrolling"
      >
        <span class="font-weight-bold">{{ menu.title }}</span>
      </v-btn>
    </template>
    <template v-else>
      <v-menu offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-btn :dark="!isScrolling" v-bind="attrs" v-on="on" icon>
            <v-icon>mdi-menu</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-item v-for="menu in navMenus" :key="menu._id" :href="`${baseUrl}/${menu.link}`">
            <v-list-item-title>{{ menu.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </template>
  </v-app-bar>
</template>

<script>
import shortid from 'shortid'
import responsive from '../mixins/responsive'

export default {
  name: 'Header',
  mixins: [responsive],
  computed: {
    getLogoUrl() {
      return this.isScrolling
        ? require('@/images/logos/logo_full_dark.png')
        : require('@/images/logos/logo_full_white.png')
    }
  },
  created() {
    window.addEventListener('scroll', this.onHandlerScroll)
    this.baseUrl = this.$route.path.includes('techcourse') ? '/techcourse' : ''
  },
  watch: {
    isScrolling() {
      this.isScrolling = window.scrollY > 0
    }
  },
  methods: {
    onHandlerScroll() {
      this.isScrolling = window.scrollY > 0
    }
  },
  data() {
    return {
      isScrolling: false,
      baseUrl: '',
      navMenus: [
        {
          _id: shortid.generate(),
          title: '소개',
          link: `about.html`
        },
        {
          _id: shortid.generate(),
          title: '교육과정',
          link: `curriculum.html`
        },
        {
          _id: shortid.generate(),
          title: '지원하기',
          link: `apply.html`
        },
        {
          _id: shortid.generate(),
          title: 'FAQ',
          link: `faq.html`
        },
        {
          _id: shortid.generate(),
          title: '문의하기',
          link: `contact.html`
        }
      ]
    }
  }
}
</script>

<style scoped>
.z-100 {
  z-index: 100 !important;
}
</style>
