<template>
  <v-dialog v-model="dialog" :persistent="persistent" :width="width">
    <template #activator="{ on }">
      <div v-on="on">
        <slot name="trigger"></slot>
      </div>
    </template>
    <v-card>
      <v-btn @click="closeDialog" class="absolute close-button" icon>
        <v-icon>mdi-close</v-icon></v-btn
      >
      <v-card-title class="headline">
        <slot name="title" />
      </v-card-title>
      <div class="mx-auto my-3">
        <slot name="image"></slot>
      </div>
      <v-card-text class="text-center overflow-y-auto pb-0">
        <slot name="text" />
      </v-card-text>
      <v-card-actions v-if="actions" class="px-5 pb-5">
        <v-spacer></v-spacer>
        <slot name="action" />
        <v-btn @click="setCookie('ncookie', 'done', 24)" text
          ><v-icon class="mr-2 body-2">mdi-close</v-icon>오늘 하루 열지 않기</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'Dialog',
  props: {
    width: {
      type: Number,
      required: false,
      default: 400
    },
    persistent: {
      type: Boolean,
      required: false,
      default: false
    },
    actions: {
      type: Boolean,
      required: false,
      default: true
    },
    close: {
      type: Boolean,
      required: false,
      default: false
    },
    toggle: {
      type: Boolean,
      required: false,
      default: false
    },
    show: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  watch: {
    close() {
      this.closeDialog()
    },
    toggle() {
      if (!this.dialog) {
        this.dialog = !this.dialog
      }
    }
  },
  created() {
    if (this.show) {
      this.dialog = true
    }
    if (document.cookie.indexOf('ncookie=done') >= 0) {
      this.dialog = false
    }
  },
  methods: {
    closeDialog() {
      this.dialog = !this.dialog
    },
    setCookie(name, value, expirehours) {
      const todayDate = new Date()
      todayDate.setHours(todayDate.getHours() + expirehours)
      document.cookie = `${name}=${escape(value)};path=/; expires=${todayDate.toGMTString()};`
      this.closeDialog()
    }
  },
  data() {
    return {
      dialog: false
    }
  }
}
</script>
<style scoped>
.close-button {
  right: 15px;
  top: 15px;
}
</style>
